import EditSelect from '@/components/edit_select2.vue'
import EditInput from '@/components/edit_input2.vue'
import EditTimeRange from '@/components/edit_time_range.vue'
import EditRadio from '@/components/edit_radio2.vue'
import ConfirmDialog from './confirmDialog.vue'
export default {
  components: {
    EditSelect,
    EditInput,
    EditTimeRange,
    EditRadio,
    ConfirmDialog
  },
  data () {
    return {
      isDialogShow: false,
      isLoadingShow: false,
      selectParamsList: [
        {
          title: '工作类型',
          list: [['未指定', '路线调查','走访', '车轮测量','跟车', '设计','观测', '检测','验收', '添乘', '办公室作业']],
          placeholder: '请选择工作类型',
          val: [],
          required: true
        },
        {
          title: '工作路局',
          list: [['武汉']],
          placeholder: '请选择工作路局',
          val: [],
          required: true
        },
        {
          title: '工作地点',
          list: [['京广高铁工务段信阳东段']],
          placeholder: '请选择工作地点',
          val: [],
          required: true
        },
        {
          title: '天气',
          list: [['晴', '雨']],
          placeholder: '请选择天气情况',
          val: [],
          required: true
        },
      ],
      inputParamsList: [
        {
          k: '详细位置',
          v: '请输入详细位置',
          required: true
        }
      ],
      timeParamsList: [
        {
          title: '上线时间',
          default_time: '请选择开始时间',
          required: true,
        },
        {
          title: '下线时间',
          default_time: '请选择结束时间',
          required: true,
        }
      ],
      timeRangeParamsList: [
        {
          title: '上线时间',
          placeholder: '请选择开始时间',
          required: true,
          startTime: '2019-03-19',
          endTime: '2019-04-19',
          val: []
        },
        {
          title: '下线时间',
          placeholder: '请选择结束时间',
          required: true,
          startTime: '2019-03-19',
          endTime: '2019-04-19',
          val: []
        },
      ],
      radioParams: {
        isOpenWindow: null,
        title: '天窗',
        required: true
      }
    }
  },
  methods: {
    onConfirmSave () {
      this.isLoadingShow = true
      let _this = this
      window.setTimeout(() => {
        this.isLoadingShow = false
        _this.$router.push('/saveMsg')
      },2000)
    }
  }
}



